@import './variables';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
}

html {
  font-size: 10px;
}

body {
  font-family: 'Work Sans', sans-serif;
  font-size: 1.5rem;
  line-height: 1.6;
  color: $color-primary;
}

.btn {
  font-family: inherit;
  font-size: 100%;
}
